@import "src/sass/base";

* {
  box-sizing: border-box;

  .skillContainer {
    //border: #037fff dashed 1px;
    width: 20%;
    min-height: 65vh;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    //padding: 15px;

    .title {
      color: #fff;
      font-size: 1.14rem;
      font-weight: 600;
      line-height: 1.857rem;
      text-transform: uppercase;
      margin-bottom: 0;
    }

    .backgroundImage {
      height: 100%;
      width: 100%;
      background-image: url('../../../assets/images/bag.png');
      //background-image: url("https://svgsilh.com/svg/576443-2196f3.svg");
      background-size: cover;
      background-position: center;
      opacity: 0.1;

    }

    .content {
      height: 80%;
      width: 10%;
      font-family: $valeraFamily;
      font-size: 18px;
      font-weight: 400;
      line-height: 1.8rem;
      color: #a4acc4;
    }
  }
}
